.navMenu{
    display: flex
}

.navBar{
    display:flex;
    justify-content: space-between;
    background: linear-gradient(to right, rgba(247, 244, 246, 1), rgba(227, 213, 185, 0));
  padding-top: 20px;
padding-bottom: 10px;
}

.pageHead{
    margin:0
}
.login, .logout{
    display: flex;
}

.link {
    text-decoration: none;
    font-weight: 700;
    font-size: 14px;
    margin-right: 15px;
    color: var(--primary-text-color);
    background-color: transparent;
    text-transform: uppercase;
}

.activeLink {
    text-decoration: none;
    font-weight: 700;
    font-size: 14px;
    margin-right: 15px;
    color: var(--secondary-text-color);
    text-transform: uppercase;
}

.helloMessage{
    font-weight: 700;
    font-size: 12px;
}



.circle{
    display: flex;
    width: 20px;
    height:20px;
    margin-top: auto;
    margin-bottom: auto;
    margin-right:10px;
    border-radius: 50%;
    background-color: var(--dark-accent-color);
}

.navLink {
    display: block;
    font-weight: 600;
    font-size: 14px;
    width: 100px;
    margin-left: 20px;
    padding-top: 3px;
    padding-bottom: 3px;
    border: none;
    border-radius: 3px;
    color: var(--primary-text-color);
    background-color: transparent;
    text-transform: uppercase;
    transition: transform 200ms linear, background-color 200ms linear, color 200ms linear;
}

.navLink:hover,
.navLink:focus {
    transform: scale(1.1);
    background-color: var(--secondary-background-color);
    color: var(--secondary-text-color)
}